import { useState } from "react";
import styled from "styled-components";

const Wrapper = styled.section`
    font-size: 24px;
    > ul {
        display: flex;

        > li {
            width: 50%;
            text-align: center;
            padding: 16px 0;
            position: relative;
            &.selected {
                &::after {
                    content: "";
                    display: block;
                    height: 3px;
                    background: #333;
                    position: absolute;
                    width: 100%;
                    bottom: 0;
                    left: 0;
                }
            }
        }
    }
`;
type Props = {
    value: "-" | "+";
    onChange: (value: "-" | "+") => void;
};
const CatagorySection: React.FC<Props> = (Props) => {
    const categroyMap = {
        "-": "支出",
        "+": "收入",
    };
    type Keys = keyof typeof categroyMap;
    const [categoryList] = useState<Keys[]>(["-", "+"]);
    const category = Props.value;
    return (
        <Wrapper>
            <ul>
                {categoryList.map((c) => (
                    <li
                        className={category === c ? "selected" : ""}
                        onClick={() => {
                            Props.onChange(c);
                        }}
                        key={c}
                    >
                        {categroyMap[c]}
                    </li>
                ))}
            </ul>
        </Wrapper>
    );
};
export { CatagorySection };
