import Layout from "components/Layout";
import { useRecords } from "hooks/useRecords";
import React, { useState } from "react";
import styled from "styled-components";
import { CatagorySection } from "./account/CatagorySection";
import { NoteSection } from "./account/NoteSection";
import { NumberPadSection } from "./account/NumberPadSection";
import { TagsSection } from "./account/TagSection";

const MyLayout = styled(Layout)`
    > div {
        display: flex;
        flex-direction: column;
    }
`;
type Category = "-" | "+";
const CategoryWrapper = styled.div`
    background-color: #c4c4c4;
`;

const defaultFormData = {
    tagIds: [] as number[],
    note: "",
    category: "-" as Category,
    amount: 0,
};

function Account() {
    const [selected, setSelected] = useState(defaultFormData);
    const { addRecords } = useRecords();
    const onChange = (obj: Partial<typeof selected>) => {
        //typeof获取值的类型 partial表示只有部分类型
        setSelected({
            ...selected,
            ...obj,
        });
    };
    const submit = () => {
        if (addRecords(selected)) {
            alert("saved!");
            setSelected(defaultFormData);
        }
    };
    return (
        <MyLayout scrollTop={9999}>
            <TagsSection
                value={selected.tagIds}
                onChange={(tagIds) => onChange({ tagIds })}
            ></TagsSection>
            <NoteSection
                value={selected.note}
                onChange={(note) => onChange({ note })}
            ></NoteSection>
            <CategoryWrapper>
                <CatagorySection
                    value={selected.category}
                    onChange={(category) => onChange({ category })}
                ></CatagorySection>
            </CategoryWrapper>

            <NumberPadSection
                value={selected.amount}
                onChange={(amount) => onChange({ amount })}
                onOk={submit}
            ></NumberPadSection>
        </MyLayout>
    );
}
export default Account;
