import { useEffect, useState } from "react";
import { createId } from "lib/createId";
import { useUpdate } from "hooks/useUpdate";
const useTags = () => {
    const [tags, setTags] = useState<{ id: number; name: string }[]>([]);
    //组件挂载时执行，获取数据
    useEffect(() => {
        let localTags = JSON.parse(window.localStorage.getItem("tags") || "[]");
        if (localTags.length === 0) {
            localTags = [
                { id: createId(), name: "衣" },
                { id: createId(), name: "食" },
                { id: createId(), name: "住" },
                { id: createId(), name: "行" },
            ];
        }
        setTags(localTags);
    }, []);
    //每次数据变化时写入localstorage，但是第一次从undefined变成[]时不需要执行写入，故使用自定义钩子
    useUpdate(() => {
        window.localStorage.setItem("tags", JSON.stringify(tags));
    }, tags);
    const findTag = (id: number) => tags.filter((tag) => tag.id === id)[0];
    const findTagIndex = (id: number) => {
        let result = -1;
        for (let i = 0; i < tags.length; i++) {
            if (tags[i].id === id) {
                result = i;
                break;
            }
        }

        return result;
    };
    const updateTag = (id: number, { name }: { name: string }) => {
        setTags(tags.map((tag) => (tag.id === id ? { id, name } : tag)));
        // const index = findTagIndex(id);
        // const tagClone = JSON.parse(JSON.stringify(tags));
        // tagClone.splice(index, 1, { id: id, name: obj.name });
        // setTags(tagClone);
    };
    const deleteTag = (id: number) => {
        setTags(tags.filter((tag) => tag.id !== id));
        // const index = findTagIndex(id);
        // const tagClone = JSON.parse(JSON.stringify(tags));
        // tagClone.splice(index, 1);
        // setTags(tagClone);
    };
    const addTag = () => {
        const tagName = window.prompt("新标签的内容为:");
        if (tagName !== null && tagName !== "") {
            setTags([...tags, { id: createId(), name: tagName }]);
        }
    };
    const getName = (id: number) => {
        const tag = tags.filter((t) => t.id === id)[0];
        return tag ? tag.name : "";
    };
    return {
        tags,
        setTags,
        findTag,
        findTagIndex,
        updateTag,
        deleteTag,
        addTag,
        getName,
    };
};

export { useTags };
//usestate只能在React组件和自定义HOOK中使用，因为这里不是函数组件，所以是一个HOOK，所以必须命名为useXXX
// 必须导出对象，只能用数组，否则报错
