import { useEffect, useRef } from "react";
const useUpdate = (fn: () => void, dependency: any[]) => {
    const count = useRef(0);
    useEffect(() => {
        count.current += 1; //避免从undefined到第一次更新时的问题
    });
    useEffect(() => {
        if (count.current > 1) {
            fn();
        }
    }, [fn, dependency]);
};

export { useUpdate };
